.order-edit-address-left-border {
  position: relative;

  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: -1px;
    border-left: 1px solid $lightGrey;
  }
}

.customer-select-wrapper {
  width: 50%;

  @media only screen and (max-width: 767px) {
    width: 100%;
  }
}
